<template>
    <router-view v-if="$store.state.chronus"></router-view>
    <chronus v-else></chronus>
</template>

<script>
import Chronus from '../../oilsdemo/src/pages/Home/Chronus/Chronus.vue'
    export default {
  components: { Chronus },
        name: '',
        data() {
            return {
                
            }
        },
        provide() {
            return {
                reload: this.reload
            }
        },
        mounted() {
            
        },

        methods: {
            reload() {
                this.alive = false
                this.$nextTick(function() { this.alive = true })
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


